*{
  font-family: 'Courier New', Courier, monospace;
}

body {
  padding: 6px;
  box-sizing: border-box;
}
header{
  background-color: #47a1f5;
  padding: 10px;
  border-bottom: 3px solid #000000;
}
body ol{
  width: 80%;
  margin: 20px auto;
}
.title{
  text-align: center;
  font-size: 1.6em;
}
input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;

}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card img{
  max-height: 150px;
}
.form-add{
  border:none;
  margin: 5px;
}
.form-add input{
  border: 1px solid #b4b4b4;
  padding: 5px 0;
}
